//  
const DataModule = {
	dataType(data) {
		const type = data.key;
		data.callType = 'socketMsg';

		//     
		switch (type) {
			case 'reload':
				this.reloadPaket(data);
				break;
			case 'alarmData':
				// console.log('', data);
				self.postMessage(data);
				// this.metricPaket(data);
				break;
			case 'alert':
				// console.log("", data);
				self.postMessage(data);
				// this.metricPaket(data);
				break;
			case 'error':
				this.eroroPaket(data);
				// this.metricPaket(data);
				break;
			default:
		}
	},
	eroroPaket(data) {
		const datas = data.data;

		console.error(datas.message);
		// console.log
		//      .
	},

	metricPaket(data) {
		const key = data.key;

		switch (key) {
			case 'metric':
				self.postMessage(data);
				break;
			default:
		}
	},

	reloadPaket(data) {
		const type = data.data;
		switch (type) {
			case 'info':
				self.postMessage(data);
				break;
			case '5s':
				self.postMessage(data);
				break;
			default:
		}
	},
};

export default DataModule;
